import {JiraAllData, JiraBlocklist, JiraEditModel, JiraSearchIssue} from "./JiraTypes";
import {Config} from "./Config";
import {JiraOrderedPriority} from "./CommonTypes";

export interface TableResultBoardEntryDevColumn {
    id: string;
    name: string;
    childCount: number;
    childDevCount: number;
    mappedStatuses: string[];
}

export interface TableResultBoardEntry {
    id: string;
    name: string;
    mappedStatuses: string[];
    childCount: number;
    devColumns: TableResultBoardEntryDevColumn[];
}

interface IssueKey {
    key: string;
}

interface IssueOrderInfo extends IssueKey {
    orderValue: number;
    priorityOrder: number;
}

export interface IssueInfo extends IssueOrderInfo {
    id: string;
    summary: string;
    priority: string;
    priorityColor: string;
    priorityUrl: string;
    status: string;
    statusId: string;
    statusChanged: number;
    assigneeName: string;
    assigneeActive: boolean;
    issueLoaded: boolean;
    blocks: string[];
}


export interface Epic extends IssueKey {
    business: BusinessIssue[];
    childCount: number;
}

export interface BusinessIssue extends IssueKey {
    devIssues: DevIssue[];
    childCount: number;
    columnPos: number;
}

export interface DevIssue extends IssueKey {
    columnPos: number;
}

export interface OrderedInfo {
    epic: Epic[],
    business: BusinessIssue[],
    dev: DevIssue[]
}

export class TableResult {

    errors: string[] = [];
    board: TableResultBoardEntry[] = [];
    epics: Epic[] = [];
    business: BusinessIssue[] = [];
    dev: DevIssue[] = [];
    statusColumns: number = 0;
    private devBoard: TableResultBoardEntryDevColumn[] = [];
    issues: { [key: string]: IssueInfo } = {};
    private statuses: { [key: string]: string } = {};
    priorities: { [p: string]: JiraOrderedPriority } = {};
    orderInfo: OrderedInfo[] = [];
    private config: Config = new Config();
    epicCount: number = 0;
    businessCount: number = 0;
    devCount: number = 0;
    devUnbusCount: number = 0;

    constructor(
        businessBoard: JiraAllData | undefined,
        devEditModel: JiraEditModel | undefined,
        config: Config | undefined,
        aPriorities: { [p: string]: JiraOrderedPriority }
    ) {

        this.priorities = aPriorities;

        if (businessBoard === undefined || devEditModel === undefined || config === undefined) {
            return;
        }

        this.config = config;

        for (let statusKey in businessBoard.entityData.statuses) {
            this.statuses[statusKey] = businessBoard.entityData.statuses[statusKey].statusName;
        }
        devEditModel.rapidListConfig.mappedColumns.forEach(col => col.mappedStatuses.forEach(st => this.statuses[st.id] = st.name));
        devEditModel.rapidListConfig.unmappedStatuses.forEach(st => this.statuses[st.id] = st.name);

        let {bBoard, dBoard} = this.fillBoards(businessBoard, devEditModel);

        if (!this.checkBoardChanges(config, bBoard, dBoard) || !this.buildBoardsLinks(config, bBoard, dBoard)) {
            return;
        }

        let epics = this.getEpics(businessBoard);
        let business: BusinessIssue[] = [];
        let epicsLinks = this.fillBusiness(businessBoard, epics, bBoard, business);

        this.board = bBoard;
        this.devBoard = dBoard;
        this.epics = epicsLinks
        this.business = business;
        this.statusColumns = bBoard
            .map(bus => (bus.devColumns.length > 0) ? bus.devColumns.length : 1)
            .reduce((accumulator, currentValue) => accumulator + currentValue, 0);
    }

    private fillNonBusinessDev(devBoardData: JiraAllData) {
        devBoardData?.issuesData?.issues?.filter(dev => !(dev.key in this.issues)).forEach(dev => {
            let devIssue: IssueInfo = {
                id: dev.id,
                key: dev.key,
                summary: dev.summary,
                status: "",
                statusId: dev.statusId,
                statusChanged: -1,
                priority: this.priorities[dev.priorityId].name,
                priorityColor: this.priorities[dev.priorityId].statusColor,
                priorityUrl: this.priorities[dev.priorityId].iconUrl,
                priorityOrder: this.priorities[dev.priorityId].order,
                orderValue: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.statusId)),
                assigneeName: "",
                assigneeActive: true,
                issueLoaded: false,
                blocks: []
            };
            this.issues[devIssue.key] = devIssue;
            this.dev.push({
                key: devIssue.key,
                columnPos: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.statusId))
            });
        });
    }

    private fillBusiness(businessBoard: JiraAllData,
                         epicInfos: { [p: string]: IssueInfo },
                         bBoard: TableResultBoardEntry[],
                         business: BusinessIssue[]) {


        let epicLinks: { [key: string]: Epic } = {};
        let usedEpics: { [key: string]: IssueInfo } = {};

        businessBoard.issuesData.issues.forEach(businessIssue => {

            const item: IssueInfo = Object.assign(businessIssue, {
                childCount: 0,
                priority: this.priorities[businessIssue.priorityId].name,
                priorityId: businessIssue.priorityId,
                priorityUrl: this.priorities[businessIssue.priorityId].iconUrl,
                priorityColor: this.priorities[businessIssue.priorityId].statusColor,
                priorityOrder: this.priorities[businessIssue.priorityId].order,
                orderValue: bBoard.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                status: this.statuses[businessIssue.statusId],
                statusChanged: businessIssue.timeInColumn.enteredStatus,
                assigneeActive: true,
                issueLoaded: false,
                blocks: []
            });
            this.issues[item.key] = item;

            if (businessIssue.epic !== undefined && businessIssue.epic in epicInfos) {

                if (!(businessIssue.epic in usedEpics)) {
                    usedEpics[businessIssue.epic] = epicInfos[businessIssue.epic];
                    epicLinks[businessIssue.epic] = {
                        key: businessIssue.epic,
                        business: [],
                        childCount: 0
                    };
                    this.issues[businessIssue.epic] = epicInfos[businessIssue.epic];
                }

                epicLinks[businessIssue.epic].business.push({
                    key: item.key,
                    devIssues: [],
                    columnPos: bBoard.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                    childCount: 0
                });
            } else {
                business.push({
                    key: item.key,
                    devIssues: [],
                    columnPos: bBoard.findIndex(bus => bus.mappedStatuses.includes(businessIssue.statusId)),
                    childCount: 0
                });
            }
        });

        return Object.values(epicLinks);
    }

    private getEpics(businessBoard: JiraAllData) {
        let epics: { [key: string]: IssueInfo } = {};
        Object.entries(businessBoard.entityData.epics).forEach(value => epics[value[1].epicField.epicKey] = {
            id: value[1].epicField.id,
            key: value[1].epicField.epicKey,
            summary: value[1].epicField.text,
            priorityOrder: Number.MAX_VALUE,
            orderValue: 0,
            priority: "",
            priorityUrl: "",
            priorityColor: "",
            status: "",
            statusId: "",
            statusChanged: -1,
            assigneeName: "",
            assigneeActive: true,
            issueLoaded: false,
            blocks: []
        });

        return epics;
    }

    private fillBoards(businessBoard: JiraAllData, devEditModel: JiraEditModel) {
        let bBoard: TableResultBoardEntry[] = businessBoard.columnsData.columns.map(value => {
            return {
                id: value.id.toString(),
                name: value.name,
                mappedStatuses: value.statusIds,
                childCount: 0,
                devColumns: []
            }
        });

        let dBoard: TableResultBoardEntryDevColumn[] = devEditModel.rapidListConfig.mappedColumns.map(value => {
            return {
                id: value.id.toString(),
                name: value.name,
                childCount: 0,
                childDevCount: 0,
                mappedStatuses: value.mappedStatuses.map(v => v.id.toString())
            };
        })

        return {bBoard, dBoard};
    }

    private checkBoardChanges(config: Config, bBoard: TableResultBoardEntry[], dBoard: TableResultBoardEntryDevColumn[]): boolean {
        let ids = bBoard.map(v => v.id);
        if (Object.keys(config.businessToDevIndexes).filter(id => !ids.includes(id)).length > 0) {
            this.errors.push("Требуется настройка: изменились колонки");
            return false;
        }

        ids = dBoard.map(v => v.id);
        if (Object.values(config.businessToDevIndexes).filter(id => !ids.includes(id)).length > 0) {
            this.errors.push("Требуется настройка: изменились колонки");
            return false;
        }
        return true;
    }

    private buildBoardsLinks(config: Config, bBoard: TableResultBoardEntry[], dBoardOrig: TableResultBoardEntryDevColumn[]): boolean {

        let dBoard: TableResultBoardEntryDevColumn[] = [];

        dBoard.push(...dBoardOrig);

        let bIndexed = bBoard.filter(value => value.id in config.businessToDevIndexes);

        // Skip first dev columns
        const firstDevIndex = config.businessToDevIndexes[bIndexed[0].id];
        while (dBoard.length > 0 && dBoard[0].id !== firstDevIndex) {
            dBoard.shift();
        }

        while (bIndexed.length > 1) {
            if (dBoard.length === 0) {
                this.errors.push("Требуется настройка: изменились колонки");
                return false;
            }

            const bus = bIndexed.shift()!;
            const nextDevIndex = config.businessToDevIndexes[bIndexed[0].id];

            while (dBoard.length > 0 && dBoard[0].id !== nextDevIndex) {
                bus.devColumns.push(dBoard.shift()!);
            }
        }

        if (bIndexed.length > 0) {
            if (dBoard.length === 0) {
                this.errors.push("Требуется настройка: изменились колонки");
                return false;
            }
            const bus = bIndexed.shift()!;
            bus.devColumns.push(...dBoard);
        }

        return true;
    }

    fillSeachedIssues(config: Config,
                      busToDevSearchIssues: JiraSearchIssue[],
                      devBoardData: JiraAllData | undefined) {

        let busIssues: { [key: string]: BusinessIssue } = {};
        this.business.forEach(b => busIssues[b.key] = b);
        this.epics.forEach(epic => epic.business.forEach(b => busIssues[b.key] = b));

        busToDevSearchIssues.forEach(bus => {
            if (bus.key in busIssues) {
                bus.fields.issuelinks.filter(link => link.type.id === config.businessToDevLinkId).map(link => link.outwardIssue)
                    .forEach(dev => {
                        let devIssue: IssueInfo = {
                            id: dev.id,
                            key: dev.key,
                            summary: dev.fields.summary,
                            status: dev.fields.status.name,
                            statusId: dev.fields.status.id,
                            statusChanged: -1,
                            priority: this.priorities[dev.fields.priority.id].name,
                            priorityColor: this.priorities[dev.fields.priority.id].statusColor,
                            priorityUrl: this.priorities[dev.fields.priority.id].iconUrl,
                            priorityOrder: this.priorities[dev.fields.priority.id].order,
                            orderValue: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.fields.status.id)),
                            assigneeName: "",
                            assigneeActive: true,
                            issueLoaded: false,
                            blocks: []
                        };
                        this.issues[devIssue.key] = devIssue;

                        busIssues[bus.key].devIssues.push({
                            key: devIssue.key,
                            columnPos: this.devBoard.findIndex(devB => devB.mappedStatuses.includes(dev.fields.status.id))
                        });

                    });
                this.fillIssueFromSearchData(config, bus);
            }
        });

        Object.values(busIssues).forEach(bus => bus.childCount = bus.devIssues.length);
        this.epics.forEach(epic => {
            epic.childCount = 0;
            epic.business.forEach(bus => epic.childCount += bus.childCount + 1);
        });

        if (this.config.showDev && devBoardData !== undefined) {
            this.fillNonBusinessDev(devBoardData);
        }
    }

    finalRecalc() {

        this.reorderData();

        this.calcStatusCounts();
    }

    private fillIssueFromSearchData(config: Config, issue: JiraSearchIssue) {
        const fields = issue.fields;
        if (fields.assignee?.displayName !== undefined) {
            this.issues[issue.key].assigneeName = fields.assignee?.displayName;
        }
        if (fields.assignee?.active !== undefined) {
            this.issues[issue.key].assigneeActive = fields.assignee?.active;
        }
        if (fields.priority?.name !== undefined) {
            this.issues[issue.key].priority = fields.priority?.name;
        }
        if (fields.priority?.id !== undefined) {
            this.issues[issue.key].priorityOrder = this.priorities[fields.priority?.id].order;
        }
        if (fields.priority?.iconUrl !== undefined) {
            this.issues[issue.key].priorityUrl = fields.priority?.iconUrl;
        }
        if (fields.status?.name !== undefined) {
            this.issues[issue.key].status = fields.status?.name;
        }
        if (config.blockField !== undefined && config.blockField !== "" && Object.hasOwn(fields, config.blockField)) {
            const block: JiraBlocklist[] = fields[config.blockField] as JiraBlocklist[];
            if (block !== undefined) {
                block.filter(block => !block.checked).forEach(block =>
                    this.issues[issue.key].blocks.push(block.name));
            }
        }
        this.issues[issue.key].issueLoaded = true;
    }

    fillIssues(config: Config, issues: JiraSearchIssue[]) {
        issues.forEach(issue => this.fillIssueFromSearchData(config, issue));
    }

    findIssuesToLoad(): string[] {

        return Object.keys(this.issues).filter(issue => !this.issues[issue].issueLoaded);
    }

    private calcStatusCounts() {

        this.epicCount = this.epics.length;
        this.epics.forEach(epic => {
            epic.business.forEach(bus => {
                this.board[bus.columnPos].childCount++;
                this.devCount += bus.devIssues.length;
            });
            this.businessCount += epic.business.length;
        });
        this.business.forEach(bus => {
            this.board[bus.columnPos].childCount++;
            this.businessCount++;
        });

        this.epics.forEach(epic => epic.business.forEach(bus => bus.devIssues.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childCount++
            }
        })));
        this.business.forEach(bus => bus.devIssues.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childCount++
            }
        }));
        this.dev.forEach(dev => {
            if (dev.columnPos >= 0) {
                this.devBoard[dev.columnPos].childDevCount++
            }
        });

        this.devUnbusCount += this.dev.length;

    }

    reorderData() {

        function compareIssues(ak: IssueKey, bk: IssueKey, p: TableResult) {
            let a: IssueOrderInfo = p.issues[ak.key];
            let b: IssueOrderInfo = p.issues[bk.key];

            if (a.priorityOrder === b.priorityOrder) {
                if (a.orderValue === b.orderValue) {
                    return a.key.localeCompare(b.key);
                } else {
                    return b.orderValue - a.orderValue;
                }
            } else {
                return a.priorityOrder - b.priorityOrder;
            }
        }

        this.epics.forEach(epic => epic.business.forEach(bus => bus.devIssues.sort((a, b) => compareIssues(a, b, this))));
        this.business.forEach(bus => bus.devIssues.sort((a, b) => compareIssues(a, b, this)));

        this.epics.forEach(epic => epic.business.sort((a, b) => compareIssues(a, b, this)));

        this.epics.forEach(epic =>
            this.issues[epic.key].orderValue = epic.business.map(bus => this.issues[bus.key].orderValue)
                .reduce((acc, val) => Math.min(acc, val)), Number.MAX_VALUE);
        this.epics.sort((a, b) => compareIssues(a, b, this));

        this.business.sort((a, b) => compareIssues(a, b, this));

        this.dev.sort((a, b) => compareIssues(a, b, this));

        this.orderInfo = [];

        Object.values(this.priorities).map(v => v.order).sort().forEach(order => {
                this.orderInfo.push({
                    epic: this.epics.filter(epic => this.issues[epic.key].priorityOrder === order),
                    business: this.business.filter(bus => this.issues[bus.key].priorityOrder === order),
                    dev: this.dev.filter(bus => this.issues[bus.key].priorityOrder === order),
                });
            }
        );
    }
}